@import url(https://fonts.googleapis.com/css?family=Lobster);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes appear{0%{opacity:0}100%{opacity:1}}@keyframes appear{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes slideIn{0%{transform:translateX(-2%)}100%{transform:translateX(0)}}@keyframes slideIn{0%{transform:translateX(-2%)}100%{transform:translateX(0)}}@-webkit-keyframes shrink{0%{width:95%}100%{width:90%}}@keyframes shrink{0%{width:95%}100%{width:90%}}

